import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {ShippingLabelComponent} from './shipping-label/shipping-label.component';
import {ShippingLabelResolver} from '../../../../Resolvers/Shipping-Label/shipping-label.resolver';

const routes: Routes = [
  {
    path: '',
    component: ShippingLabelComponent,
    resolve: {
      ChargedOrders: ShippingLabelResolver
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ShippingLabelRoutingModule {
}
