import {Component, OnInit} from '@angular/core';
import {ShippingLabelUserData} from '../../../../../Models/Interfaces/ShippingLabel/ShippingLabelUserData';
import {ActivatedRoute} from '@angular/router';
import {ResponseCodeEnum} from '../../../../../../../../../shared/shared-enums/ResponseCodeEnum';
import {ERequestTypes} from '../../../../../Models/Enums/RequestTypes/RequestTypes';
import {ShippingLabelService} from '../../../../../Services/ShippingLabelService/shipping-label.service';
import {IAddShippingLabel} from '../../../../../Models/Interfaces/ShippingLabel/AddShippingLabel';
import {ServiceNameEnum} from '../../../../../../../../../shared/shared-enums/ServiceNameEnum';
import {ToastrService} from 'shared/shared-services/toastr.service';
import {IDownloadShippingLabelFile} from '../../../../../Models/Interfaces/ShippingLabel/DownloadShippingLabelFile';
import {DownloadService} from '../../../../../../../../../shared/shared-services/download.service';
import {SubOrderType} from 'shared/shared-static-data/SubOrderType';
import {IApiData} from '../../../../../Models/Interfaces/APIs/apiData.model';
import {OrderListService} from '../../../../../Services/OrderServices/OrderListService/order-list.service';

@Component({
  selector: 'app-shipping-label',
  templateUrl: './shipping-label.component.html',
  styleUrls: ['./shipping-label.component.css']
})
export class ShippingLabelComponent implements OnInit {
  shippingLabelUserData = {} as ShippingLabelUserData;
  shippingOBJ = {requestType: ERequestTypes.ADD, services: []} as IApiData<IAddShippingLabel>;
  orderId!: number;
  downloadFile = {requestType: ERequestTypes.VIEW, services: []} as IApiData<IDownloadShippingLabelFile>;
  /**
   * The Requirements For The Construction Of The Component
   * @param route For using activated route
   * @param shippingLabelService for using ShippingLabelService
   * @param toastService To show request notifications
   * @param downloadService for using DownloadService
   * @param orderListService
   */
  constructor(
    private route: ActivatedRoute,
    private shippingLabelService: ShippingLabelService,
    private toastService: ToastrService,
    private downloadService: DownloadService,
    private orderListService: OrderListService,
  ) {
  }
  /**
   * onInit get user data
   * @return void
   */
  ngOnInit(): void {
    this.getUserData();
    this.orderListService.clearSearchData();
  }
  /**
   * get user data
   * @return void
   */
  getUserData(): void {
    this.route.data.subscribe(res => {
      if (res.ChargedOrders.responsCode === ResponseCodeEnum.OK) {
        this.shippingLabelUserData = res.ChargedOrders.data.VUsersCN[0];
      }
    });
  }
  /**
   * on submit add shipping label
   * @return void
   */
  submit(event: any): void {
    this.shippingOBJ.services[0] = {
      serviceName: ServiceNameEnum.ORDER_ADD_SHIPPING_LABEL,
      ConditionsAnded: false,
      columns: [],
      shippingLabelData: {
        name: event.fullName,
        city: event.city,
        countryCode: event.country,
        email: event.email,
        address: event.address,
        telephone: event.phone,
        stateOrProvinceCode: event.state,
        timeToShip: event.timeToShip,
        postalCode: event.zipCode,
      }
    };
    this.shippingLabelService.addShippingLabel(this.shippingOBJ).subscribe(res => {
      if (res.responsCode === ResponseCodeEnum.OK) {
        this.orderId = res.reponses[0].data[0].orderID;
        this.toastService.addAlert('Added Successfully', 'Shipping Label', 'start');
      }
    });
  }
  /**
   * download files
   * @return void
   */
  onDownloadFle(): void {
    this.downloadFile.services[0] = {
      columns: [],
      ConditionsAnded: false,
      isPortable: false,
      orderID: this.orderId,
      serviceName: ServiceNameEnum.DOWNLOAD_FILE,
      suborderEnum: SubOrderType.MISC.value
    };
    this.shippingLabelService.onDownloadFle(this.downloadFile).subscribe(res => {
      this.downloadService.downLoadFile(res);
    });
  }
}

